.article {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 772px;
  margin: 20px;
  padding: 40px;
  border-radius: 5px;
  color: #bbb;
  background-color: #FFF;
}
.article .article-header .article-title {
  text-align: center;
  font-size: 25px;
  color: #444;
  font-weight: 700;
}
.article .article-header .article-title a {
  color: #444;
  text-decoration: none;
}
.article .article-header .article-info {
  font-size: 14px;
  color: #555;
}
.article .article-body {
  margin: 10px 0;
}
.article .article-body .content {
  font-size: 14px;
  color: #555;
}
.article .article-footer a {
  border-radius: 5px;
  padding: 5px 15px;
  color: #FFF;
  font-size: 13px;
  background-color: #97dffd;
  text-decoration: none;
}
.article .article-footer a:hover {
  background-color: rgba(0, 149, 255, 0.2);
}



