html,
body {
  padding: 0;
  margin: 0;
  background-color: rgba(240, 240, 240, 0.6);
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
#app {
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
#app .header {
  position: relative;
  width: 100%;
}
#app .header .nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}
#app .header .wrapper {
  height: 600px;
  width: 100%;
}
#app .main {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
}
#app .footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
#app .footer .footer-image {
  width: 100%;
  height: 400px;
  background-image: url(assets/img/footer-image.jpg);
  background-size: cover;
  background-position: center 50%;
  background-repeat: no-repeat;
}
#app .footer .footer-inner {
  padding: 10px;
  color: #888;
  background-color: #232323;
  text-align: center;
}
#app .footer .footer-inner a {
  color: #795548;
}



