.nav {
  display: flex;
  background-color: rgba(40, 42, 44, 0.6);
  width: 100%;
  height: 38px;
  line-height: 38px;
}
.nav .link {
  display: block;
  padding: 0 38px;
  color: #fff;
}
.nav .link:link {
  text-decoration: none;
}
.nav .link:hover {
  color: rgba(40, 42, 44, 0.6);
  background-color: #fff;
}
.nav-show {
  transform: none;
  transition: transform 300ms ease;
}
.nav-hide {
  transform: translateY(-100%);
  transition: transform 300ms ease;
}



